<template>
  <div>
      <mt-cell id="extend" title="延长时间" is-link></mt-cell>
      <mt-cell id="price" title="总价"></mt-cell>
      <mt-cell id="pay" title="支付方式" is-link></mt-cell>

      <mt-button id="confirm">确认</mt-button>

      <mt-picker vue-if={{ "flase" }} id="picker" :slots="slots" @change="onValuesChange"></mt-picker>
  </div>
  
</template>

<script>
import Vue from "vue";
import { Cell, Button, Picker } from "mint-ui";

Vue.component(Cell.name, Cell);
Vue.component(Button.name, Button);
Vue.component(Picker.name, Picker);

export default {
  name: "ExtendOrder",
  data() {
    return {
            slots: [
        {
          flex: 1,
          values: ['Apple Pay','八达通','Ali Pay'],
          textAlign: 'center'
        }
      ]
    }
  },
}
</script>

<style>

#confirm{
  background-color: #3e88fe;
  color: white;
  width: 80%;
  margin-left: 10%;
  margin-top: 40px;
}

.mint-cell{
  text-align: left;
}

#picker {
  position: absolute;
  bottom: 0;
  width: 100%;
}

</style>


